$color-black: rgb(3,3,3);
$color-green: rgb(0,118,31);
$color-red: rgb(172,25,9);
$color-grey: rgb(52,52,52);
$color-grey-2: rgb(57,57,57);
$color-grey-dark: rgb(18,18,18);
$color-grey-dark-2: rgb(23,23,23);
$color-grey-light: rgb(212,212,212);
.App {
    text-align: center;
    position: relative;
    background-color: $color-grey;
    height:100vh;
    width:100%;
  }
  * {
    margin:0;
    padding:0;
  }
  .header {
    margin-bottom:20px;
    color:white;
  }
  .game {
    padding:20px;
    background-color: $color-grey-dark;
  }
  .display {
    background-color: $color-green;
    padding-left:40px;
    border:2px solid black;
  }
  .board{
    background-color: $color-green;
    position: relative;
    padding:20px 20px 20px 60px;
  } 
  
  
  body{
    font-size: 18px;
  }
  .button{
    border-radius: 5px;
    border:2px solid $color-grey-light;
    background-image: linear-gradient(
        to right bottom,
        rgba($color-grey, 0.80),
        rgba($color-grey-2, 0.80)); 
    padding:15px 30px;
    margin:0px 10px;
    color:white;
    text-transform: uppercase;
    font-weight:bold;
    font-size: 22px;
    transform: translate(-1px)
  }
  .button:hover{
    transform:translateY(-2px);
  }
  .button:active{
    transform: translate(0);
  }
  .stats{
    &__panel{
      display:flex;
      justify-content: center;
      height:80px;
      padding-top:20px;
      padding-right:40px;
    }
    &__label {
      color: $color-black;
      font-size:24px;

      &--player {
        line-height:40px;
      }
    }
    &__display {
      width:33%;
      font-weight: bold;
      border:2px solid white;
      
    }
    &__spin {
      width:33%;
      font-weight: bold;
      border:2px solid white;
      border-left:none;
      &--label {
        display:flex;
        justify-content: center;
        margin-bottom:5px;
      }
    }
    &__amount--label{
      font-size:20px;
      font-weight: 700;
      margin-left:5px;
    }
    &__result {
      width:33%;
      border: 2px solid white;
      border-left: none;
      font-size:12px;
      
      &--display {
        font-size:26px;
      }
      &--label{
        font-size:20px;
        font-weight: bold;
        color:white;
      }
      &--message {
        color: white;
      }
      &--amount {
        color: $color-grey-dark;
        font-size:24px;
        background-color:$color-grey-light;
        border-radius: 5px;
      }
    }
  }
  
  .spin-result {
    color:white;
    width:50%;
    height:40px;
    margin: 0 auto;
    
  }
  .result__green{
    color: $color-green;
    
  }
  .result__red {
    color: $color-red;
  }
  .result__black {
    color: $color-black;
  }
  
  .top-row {
    display: flex;
    justify-content: center;
    
  }
  .middle-row {
    display: flex;
    justify-content: center;
  }
  .bottom-row {
    display: flex;
    justify-content: center;
  }
  .row-bet{
    width:80px;
    height:80px;
    border:1px solid white;
    background-color: $color-green;
    color: white;

    
  }
  
  
  .zero__bet{
    width:80px;
    height:245px;
    background-color: $color-green;
    position: absolute;
    left:-81px;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    color:white;
    line-height:240px;
    border: 1px solid white;
    
  }
  .side-line-1{
    display: flex;
   
  }
  .side-line-2{
    display: flex;
    
  }
  .board__chips {
    position: relative;
  }
  .thirds-bet{
    width:206px;
    height:40px;
    border:1px solid white;
    background-color: $color-green;
    color: white;
    display:flex;
    justify-content: center;
    

    &__height{
      line-height:40px;
      margin-right:10px;
    }
  }
  .one-of-two-bet{
    width:102px;
    height:40px;
    border: 1px solid white;
    background-color: $color-green;
    color: white;
    display: flex;
    justify-content: center;


    &__height{
      line-height:40px;
      margin-right:10px;
    }
  }
  
  
  .board-number {
    width:50px;
    height:80px;
    border: 1px solid white;
    align-self: center;
    color: white;
  }
  

  .red{
    background-color: $color-red;
  }
  .black{
    background-color: $color-black;
  }
  .selected {
    background-color: blue;
  }
  .green{
    background-color: $color-green;
  }
  
  /* betting */
  .bet__options{
    display:flex;
    justify-content: center;
    margin-top:10px;
    height:100px;
    margin-bottom:10px;

    &--label{
      color: white;
      line-height:80px;
      font-size: 22px;
      margin-right:10px;
      
    }
  }
  .bet__image{
    padding:10px;
    width:75px;
  }
  .bet__chips {
    
  }
  .bet__selected {
    border:2px solid $color-grey-light;
    
  }
  
  .chip__small {
    width:40px;
    height:40px;
  }
  
  .screen{
    display:flex;
    justify-content: center;
  }
  
  .wheel__image {
    width:500px;
    height:500px;
    margin-left:100px;
  }
  

  
  .wheel__display {
    position: relative;
  }
  
